export default {
  name: 'finishedTask',
  props: {
    task: []
  },
  data () {
    return {
      tag: this.Finished
    }
  },
  methods: {
    goTask (taskName, taskID) {
      sessionStorage.setItem('taskName', taskName)
      sessionStorage.setItem('taskID', taskID)
      this.$router.push('/task')
    }
  }
}



